
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import { useForm } from 'react-hook-form';
import { toast } from "react-toastify";

import { getCaregiverDemoQuestions, addCaregiverDemoQuestions } from '../../store/actions/question.action.js';

import { useFormContext } from '../../context/FormContextProvider.js';
import { initialQuestions } from './constants.js';
import { PageCard } from "../commonComponents/Cards/HelperCards";
import { useTitle, useMediaQuery } from '../../helpers/custom-hooks.js';
import { validateChildren, processCaregiverDemoOptions } from '../../helpers/form.helper.js';


const CaregiverDemographicWrapper = () => {
	const navigate = useNavigate();
	const { checkFormProgress_c, getFormProgress_c } = useFormContext();
	const FORM_PROGRESS = getFormProgress_c();
	const [isNavigationNeeded, navigateTo] = checkFormProgress_c(FORM_PROGRESS.caregiver);

	useEffect(() => {
		// check the progress of the form being filled
		if (isNavigationNeeded) {
			navigate(navigateTo);
		}
	}, []);

	return (
		<>
			{isNavigationNeeded ? null : <CaregiverDemographic />}
		</>
	);
};

const CaregiverDemographic = () => {
	useTitle('Caregiver Demographics');

	const navigate = useNavigate();
	const [alert, setAlert] = useState(true);
	const [isLoading, setIsLoading] = useState(false);
	const [questions, setQuestions] = useState([]);
	const [noOfChildren, setNoOfChildren] = useState(0);
	const { setFormProgress_c, getPatientId_c, getFormProgress_c, getNavigateTo_c } = useFormContext();
	const FORM_PROGRESS = getFormProgress_c();
	const NAVIGATE_TO = getNavigateTo_c();
	const [children, setChildren] = useState([
		{
			age: '',
			education_level: ''
		}
	]);
	const isDesktop = useMediaQuery('(min-width: 992px)');
	const { register, handleSubmit, formState: { errors } } = useForm();

	const patient_id = getPatientId_c();

	useEffect(() => {
		_getCaregiverDemoQuestions();
		window.scrollTo(0, 0);
	}, []);


	const handleData = async (data) => {
		// this is data after it is validated
		if (!validateChildren(children, noOfChildren)) {
			toast.error('Please fill all the fields of children');
			return;
		}

		setIsLoading(true);

		const finalOptions = processCaregiverDemoOptions({ data, children, noOfChildren, patient_id });

		// sending data to backend
		const response = await addCaregiverDemoQuestions(finalOptions);
		if (response.status === 200) {
			// updating the form progress
			setFormProgress_c(FORM_PROGRESS.child);
			navigate(NAVIGATE_TO.child);
		} else if (response.status >= 400) {
			toast.error(response.message?.message);
		}
		setIsLoading(false);
	};

	async function _getCaregiverDemoQuestions() {
		setIsLoading(true);
		const caregiverDemoQuestions = await getCaregiverDemoQuestions();

		if (caregiverDemoQuestions.status === 200) {
			setQuestions(caregiverDemoQuestions.data);
			setIsLoading(false);
		} else if (caregiverDemoQuestions.status >= 400) {
			toast.error(caregiverDemoQuestions.message.message);
		}

	}



	return (
		<PageCard title="Caregiver Demographics" isLoading={isLoading} >



			{alert ? <Alert variant="info" dismissible onClose={() => setAlert(false)} >
				<Alert.Heading>Caregiver Demographics</Alert.Heading>
				<hr />
				<p className="mb-0">
					This is caregiver's data form. Please only fill caregiver's demographics here.
				</p>
			</Alert> : null}


			<div style={isLoading ? { pointerEvents: `none`, opacity: 0.5 } : {}}>
				<Form onSubmit={handleSubmit(handleData)}>


					{/* These are hardcoded questions. arounf 4-5 that collect general information about the target */}
					{initialQuestions.map((formGroup, index) => (
						<Form.Group className="mb-3" controlId={formGroup.name} key={index}>
							<Form.Label className='row d-flex justify-content-between'>
								<div className={`${formGroup.urdu_label.length > 0 ? 'col-md-6' : 'col-12'}`}>
									<b>
										{`${formGroup.label} : `}{formGroup.required ? <span className='text-danger'>*</span> : null}
									</b>
								</div>
								{formGroup.urdu_label.length > 0 ?
									<div className='col-md-6 text-end'>
										<b>
											{formGroup.required ? <span className='text-danger'>*</span> : null}{` : ${formGroup.urdu_label}`}
										</b>
									</div>
									: null
								}
							</Form.Label>
							<div className='px-10'>
								{formGroup.type === 'radio' ?
									<Form.Select
										key={index}
										id={`${formGroup.name}-${index}`}
										{...register(formGroup.name, { required: formGroup.required })}
									>
										<option value="" key='0'>Select</option>
										{formGroup.radio_options.map((option, index) => (
											<option key={index} value={option}>{option}</option>
										))}

									</Form.Select>
									:
									<Form.Control
										type={formGroup.type}
										min={0}
										{...register(formGroup.name, { required: formGroup.required })}
									/>
								}
							</div>
							{errors[formGroup.name] && <span className="text-danger">This field is required</span>}
						</Form.Group>
					))}



					{/* custom feilds for children data  - Start */}
					{/* first question: number of children */}
					<Form.Group className="mb-3" controlId="no_of_children">
						<Form.Label><b>{`No of children : `}<span className='text-danger'>*</span></b></Form.Label>
						<div className='px-10'>
							<Form.Control
								type='number'
								value={noOfChildren}
								onChange={(e) => {
									setNoOfChildren(Number(e.target.value));
									setChildren([...Array(Number(e.target.value)).keys()].map((_, i) => ({
										age: '',
										education_level: ''
									})));
								}}
								min={0}
								required
							/>
						</div>
					</Form.Group>

					{/* if number of children are greater than 0 then ask ages of every children and their education level  */}
					{noOfChildren > 0 ? children.map((child, index) => (
						<div key={index} className='row'>

							<div className='col-4'>
								<Form.Group className="mb-3" controlId="age">
									<Form.Label><b>{`Age of child ${index + 1}: `}</b></Form.Label>
									<div className={`${isDesktop ? 'px-10' : 'px-2'}`}>
										<Form.Control
											type='text'
											onChange={(e) => {
												const newChildren = [...children];
												newChildren[index].age = e.target.value;
												setChildren(newChildren);
											}}
										/>
									</div>
								</Form.Group>
							</div>

							<div className='col-8'>
								<Form.Group className="mb-3" controlId="education_level">
									<Form.Label><b>{`Education level of child ${index + 1} : `}</b></Form.Label>
									<div className={`${isDesktop ? 'px-10' : 'px-2'}`}>
										<Form.Control
											type='text'
											onChange={(e) => {
												const newChildren = [...children];
												newChildren[index].education_level = e.target.value;
												setChildren(newChildren);
											}}
										/>
									</div>
								</Form.Group>
							</div>

						</div>
					)) : null}
					{/* custom feilds for children data  - End */}



					{/* These are rest af the quesrions fetched from the database */}
					{questions.map((formGroup, index) => (
						<Form.Group className="mb-3" controlId={formGroup.id} key={index} >
							<Form.Label className='row d-flex justify-content-between'>
								<div className={`${formGroup.question_urdu_text.length > 0 ? 'col-md-6' : 'col-12'}`}>
									<b>
										{`${formGroup.question_text} : `}{!formGroup.is_nullable ? <span className='text-danger'>*</span> : null}
									</b>
								</div>
								{formGroup.question_urdu_text.length > 0 ?
									<div className='col-md-6 text-end'>
										<b>
											{!formGroup.is_nullable ? <span className='text-danger'>*</span> : null}{` : ${formGroup.question_urdu_text}`}
										</b>
									</div>
									: null
								}
							</Form.Label>
							<div className='px-10'>
								{
									formGroup.is_text_type ?
										<>
											<Form.Control
												type='text'
												{...register(formGroup.id.toString(), { required: !formGroup.is_nullable })}
											/>
										</>
										:
										formGroup.is_number_type ?
											<>
												<Form.Control
													type='number'
													min={0}
													{...register(formGroup.id.toString(), { required: !formGroup.is_nullable })}
												/>
											</>
											:
											formGroup.is_date_type ?
												<>
													<Form.Control
														type='date'
														{...register(formGroup.id.toString(), { required: !formGroup.is_nullable })}
													/>
												</>
												:
												formGroup.is_radio_type ?
													<>

														<Form.Select
															// type='radio'
															id={`${formGroup.id}-${index}`}
															{...register(formGroup.id.toString(), { required: !formGroup.is_nullable })}
														>
															<option value="" key='0'>Select</option>
															{formGroup.radio_options?.split(',').map((option, i) => (
																<>
																	<option key={`default-${formGroup.id}-${i}`} value={option}>{option}</option>
																</>
															))}
														</Form.Select>
													</>
													: null
								}
							</div>
							{errors[formGroup.id.toString()] ? <span className="text-danger">This field is required</span> : null}
						</Form.Group>
					))}


					<Form.Group className="mb-3">
						<Button className="mb-3 float-end" variant="primary" type="submit">
							Next
						</Button>
					</Form.Group>

				</Form>
			</div>
		</PageCard >
	);
};

export default CaregiverDemographicWrapper;